/*COLOR*/
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.page {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1280px;
  font-family: "PT Sans", "Arial", sans-serif; }
  @media (min-width: 768px) {
    .page {
      width: 768px; } }
  @media (min-width: 1280px) {
    .page {
      width: 1280px;
      padding: 0 40px; } }

.page__main-index {
  display: flex;
  flex-direction: column;
  text-transform: uppercase; }

.button {
  display: block;
  padding: 15px 32px 17px 32px;
  margin: 0 20px;
  font-size: 21px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2; }

.nav__closed .nav__list {
  display: none; }

.nav__wrapper-list {
  z-index: 2; }

.nav__wrapper-logo {
  position: relative;
  text-align: center; }
  @media (min-width: 768px) {
    .nav__wrapper-logo {
      top: -26px;
      left: 43%;
      display: inline-block; } }
  @media (min-width: 1280px) {
    .nav__wrapper-logo {
      left: 46%;
      display: inline-block; } }

.nav__list {
  margin: 0;
  padding: 0;
  background-color: #81b3d2;
  list-style: none; }
  @media (min-width: 768px) {
    .nav__list {
      display: flex;
      justify-content: space-between;
      background-color: #ffffff; } }

.nav__item {
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2); }
  .nav__item:last-child {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%); }
  @media (min-width: 768px) {
    .nav__item {
      width: 20%;
      box-shadow: none; }
      .nav__item:nth-child(2) {
        margin-right: 10%; }
      .nav__item:nth-child(3) {
        margin-left: 10%; } }

.nav__link {
  display: inline-block;
  padding: 11px 0 5px 0;
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  vertical-align: middle;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase; }
  @media (min-width: 768px) {
    .nav__link {
      font-size: 14px;
      line-height: 26px;
      color: #000000; } }

.nav__button {
  position: absolute;
  padding: 0;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: none; }
  @media (min-width: 768px) {
    .nav__button {
      display: none; } }

.nav__button--close {
  top: 17px;
  right: 20px; }

.nav__button--open {
  top: 17px;
  right: 20px; }

