.top {
  margin-top: -33px;
  min-height: 389px;
  text-align: center;
  background: url('../img/bg-back-mobile@1x.jpg') no-repeat;
  background-position: center;

  @media (min-resolution: 2dppx) {
    background: url('../img/bg-back-mobile@2x.jpg') no-repeat;
    background-size: 768px;
    background-position: center;
  }

  @media (min-width: $tablet-width) {
    margin-top: -57px;
    min-height: 518px;
    background: url('../img/bg-back-tablet@1x.jpg') no-repeat;

    @media (min-resolution: 2dppx) {
      background: url('../img/bg-back-tablet@2x.jpg') no-repeat;
      background-size: 768px;
    }
  }

  @media (min-width: $desktop-width) {
    background: url('../img/bg-back-desktop@1x.jpg') no-repeat;
    background-size: 100%;

    @media (min-resolution: 2dppx) {
      background: url('../img/bg-back-desktop@2x.jpg') no-repeat;
      background-size: 100%;
    }
  }
}

.top__img {
  margin-top: 90px;

  @media (min-width: $tablet-width) {
    width: 498px;
    height: 376px;
  }
}
